<template>
  <div class="VitaBox">
    <div class="swiper swiper-container">
      <div class="swiper-wrapper">
        <!-- 1 -->
        <div class="swiper-slide SwiperOne">
          <div class="OneBox">
            <div class="title">
              <div class="avatar">
                <img src="@/img/avatar.jpg" alt="" />
              </div>
              <h2></h2>
              <hr style="width: 80%; margin: 0 auto; margintop: 10px" />
              <p></p>
            </div>
          </div>
        </div>
        <!-- 2 -->
        <div class="swiper-slide SwiperTwo">
          <div class="TwoBox">
            <div class="title">
              <h3></h3>
              <div class="iconBox">
                <!-- for -->
                <div class="iconOne" v-for="o in TwoList" :key="o.id">
                  <span
                    class="iconfont"
                    :class="o.class"
                    :style="{ color: o.color }"
                  ></span>
                  <p>{{ o.text }}</p>
                </div>
              </div>
              <hr style="width: 80%; margin: 0 auto; marginBottom: 20px" />
              <div class="textBox">
                <p>
                  
                </p>
                <p></p>
                <p></p>
              </div>
            </div>
          </div>
        </div>
        <!-- 3 -->
        <div class="swiper-slide SwiperThree">
          <div class="ThreeBox">
            <div class="title">
              <h3></h3>
              <div class="iconBox">
                <div class="iconOne" v-for="J in threeList" :key="J.id">
                  <span
                    class="iconfont"
                    :class="J.class"
                    :style="{ color: J.color }"
                  ></span>
                  <p>{{ J.text }}</p>
                </div>
              </div>
              <hr style="width: 80%; margin: 0 auto; marginBottom: 20px" />
              <div class="text">
                <p> web , HTML/CSS/JS , PSD </p>
                <p> vue ,(ElementUIbootstrap)</p>
                <p>webpackless</p>
                <p>Git</p>
                <p>Nodeeslint</p>
              </div>
            </div>
          </div>
        </div>
        <!-- 4 -->
        <div class="swiper-slide SwiperFour">
          <div class="FourBox">
            <div class="title">
              <h3></h3>
              <div class="proBox">
                <el-card
                  class="box-card"
                  v-for="item in textList"
                  :key="item.id"
                >
                  <div class="text item">
                    <h4>{{ item.h4 }}</h4>
                    <p><strong></strong>{{ item.refer }}</p>
                    <p><strong></strong>{{ item.art }}</p>
                    <p><strong></strong>{{ item.job }}</p>
                    <p><strong></strong>{{item.yeji}}</p>
                    <el-button
                      v-if="item.index"
                      type="primary"
                      round
                      @click="goGame"
                      ></el-button
                    >
                  </div>
                </el-card>
              </div>
            </div>
          </div>
        </div>
        <!-- 5 -->
        <div class="swiper-slide SwiperFive">
