<script>
import 'swiper/dist/js/swiper'
import 'swiper/dist/css/swiper.css'
import Swiper from 'swiper'
import Clipboard from 'clipboard'
export default {
  name: 'MyHome',
  data () {
    return {
      // 
      TwoList: [
        { id: 0, color: '#ff80b1', class: 'icon-nianling', text: '22' },
        { id: 1, color: '#0197d1', class: 'icon-xueli', text: '' },
        { id: 2, color: '#fc490b', class: 'icon-zuobiaofill', text: '' }
      ],
      // 
      threeList: [
        { id: 0, color: '#fc490b', class: 'icon-html', text: 'HTML' },
        { id: 1, color: ' #2196f3', class: 'icon-css', text: 'CSS' },
        { id: 2, color: ' #f4de51', class: 'icon-js', text: 'JS' },
        { id: 3, color: ' #41b883', class: 'icon-Vue', text: 'Vue' },
        { id: 4, color: ' #f05030', class: 'icon-git', text: 'Git' }
      ],
      // 
      textList: [
        {
          id: 0,
          h4: '',
          refer:
            '',
          art: 'Vue2 + node + Element-UI + Axios + git +Echarts',
          job: 'PCIPADAxios',
          yeji: 'vue-ui20%,'
        },
        {
          id: 1,
          index: true,
          h4: 'DREADFUL HAZY_|',
          refer:
            'DREADFUL HAZY_|',
          art: 'Vue2 + Element-UI + git',
          job: 'pc',
          yeji: '80%'
        }
      ],
      // 
      fiveList: [
        {
          id: 0,
          content: 'S207789',
          color: '#28c445',
          class: 'tag-weChat',
          IconClass: 'icon-weixin',
          click: 'weChat'
        },
        {
          id: 1,
          content: '2250832184',
          color: '#30a5dd',
          class: 'tag-QQ',
          IconClass: 'icon-QQ',
          click: 'QQ'
        },
        {
          id: 2,
          content: '15216173039',
          color: '#ff9000',
          class: 'tag-Phone',
          IconClass: 'icon-dianhuatianchong',
          click: 'Phone'
        },
        {
          id: 3,
          content: '2250832184@qq.com',
          color: '#51a9ff',
          class: 'tag-email',
          IconClass: 'icon-youxiang',
          click: 'email'
        }
      ]
    }
  },
  mounted () {
    // eslint-disable-next-line no-new
    new Swiper('.swiper-container', {
      // 
      direction: 'vertical',
      mousewheel: true, // 
      // 
      pagination: {
        el: '.swiper-pagination',
        clickable: true, // 
        dynamicBullets: true
      }
    })
  },
  methods: {
    goGame () {
      window.location.href = 'https://kaixliu.github.io/game_web/dist'
    },
    // 
    LXW (method) {
      if (method === 'weChat') {
        const clipboard = new Clipboard('.tag-weChat')
        clipboard.on('success', (e) => {
          this.$message.success('')
          // 
          clipboard.destroy()
        })
        clipboard.on('error', (e) => {
          // 
          this.$message.error('')
          // 
          clipboard.destroy()
        })
      } else if (method === 'QQ') {
        const clipboard = new Clipboard('.tag-QQ')
        clipboard.on('success', (e) => {
          this.$message.success('QQ')
          // 
          clipboard.destroy()
        })
        clipboard.on('error', (e) => {
          // 
          this.$message.error('')
          // 
          clipboard.destroy()
        })
      } else if (method === 'Phone') {
        const clipboard = new Clipboard('.tag-Phone')
        clipboard.on('success', (e) => {
          this.$message.success('')
          // 
          clipboard.destroy()
        })
        clipboard.on('error', (e) => {
          // 
          this.$message.error('')
          // 
          clipboard.destroy()
        })
      } else if (method === 'email') {
        const clipboard = new Clipboard('.tag-email')
        clipboard.on('success', (e) => {
          this.$message.success('')
          // 
          clipboard.destroy()
        })
        clipboard.on('error', (e) => {
          // 
          this.$message.error('')
          // 
          clipboard.destroy()
        })
      }
    }
  }
}
</script>

